  }
  
  
  figcaption,
  figure,
  main {
  
    display: block;
  }
  
  figure {
    margin: 1em 40px;
  }
  
  hr {
    box-sizing: content-box;
  
    height: 0;
  
    overflow: visible;
  
  }
  
  pre {
    font-family: monospace, monospace;
  
    font-size: 1em;
  
  }
  
  
  a {
    background-color: transparent;
  
    -webkit-text-decoration-skip: objects;
  
  }
  
  a:active,
  a:hover {
    outline-width: 0;
  }
  
  abbr[title] {
    border-bottom: none;
  
    text-decoration: underline;
  
    text-decoration: underline dotted;
  
  }
  
  b,
  strong {
    font-weight: inherit;
  }
  
  b,
  strong {
    font-weight: bolder;
  }
  
  code,
